* {
    box-sizing: border-box;
}
img:hover {
    border-radius: 10px;
    box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}
div.project {
    box-sizing: border-box;
    text-align: center;
    padding: 10px;
    position: relative;
}

div.projects {
   
    text-align: center;
    position: absolute;
    font-size: 2ch;
    background-color: rgba(65, 65, 65, 0.623);
    border: rgba(65, 65, 65, 0.267);
    border-radius: 2%;
    display: grid;
   
}

